<template>
  <div>
    <b-card class="d-none d-sm-block">
      <b-table
        :per-page="perPage"
        :current-page="currentPage"
        responsive
        :items="items"
        :fields="fields"
        class="mb-0 account-details-table"
      />
      <b-pagination
        v-model="currentPage"
        :total-rows="totalRows"
        :per-page="perPage"
        align="center"
        size="sm"
        class="my-0"
      />
    </b-card>
    <b-card class="d-block d-sm-none">
      <b-table
        stacked
        :per-page="perPage"
        :current-page="currentPage"
        responsive
        :items="items"
        :fields="fields"
        class="mb-0 account-details-table"
      />
      <b-pagination
        v-model="currentPage"
        :total-rows="totalRows"
        :per-page="perPage"
        align="center"
        size="sm"
        class="my-0"
      />
    </b-card>
  </div>
</template>

<script>
import {
  BCard, BCardText, BLink, BAlert, BRow, BCol, BTable, BPagination,
} from 'bootstrap-vue'
import axios from '@/libs/axios'

export default {
  name: 'AccountDetails',
  components: {
    BCard,
    BCardText,
    BLink,
    BAlert,
    BRow,
    BCol,
    BTable,
    BPagination,
  },
  data() {
    return {
      perPage: 10,
      pageOptions: [3, 5, 10],
      totalRows: 1,
      currentPage: 1,
      fields: [],
      items: [],
    }
  },
  mounted() {
    let token
    token = sessionStorage.getItem('token') ? sessionStorage.getItem('token') : localStorage.getItem('token')
    axios
      .get('https://sm.oop.ir/api/v1/account-detail', {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then(response => {
        this.items = response.data.list
        const headers = response.data.header
        for (const header in headers) {
          this.fields.push(
            {
              key: header,
              label: headers[header],
            },
          )
        }
        this.totalRows = this.items.length
      })
      .finally(() => {})
  },

}
</script>

<style>
[dir=rtl] .table.b-table.account-details-table.b-table-stacked > tbody > tr > [data-label]::before {
  float: right;
  padding: 0 0 0 calc(1rem / 2);
  text-align: inherit;
}
</style>
